import { base } from "store/store";

export const AI_login = base.createInstance({
  endpoint: "/login",
  reducer: "user",
  reduxKey: "userDetail",
  action: "LOGIN_USER",
  method: "post",
});

export const AI_signup = base.createInstance({
  endpoint: "/signup",
  reducer: "user",
  reduxKey: "signup",
  action: "SIGNUP_USER",
  method: "post",
});